import {Button,TextField,Grid, FormControl,FormHelperText, CardContent, CardMedia} from '@mui/material';
import {Box,OutlinedInput,Alert} from '@mui/material';
import {Select, MenuItem,InputLabel,Card} from '@mui/material'
import Typography from '@mui/material/Typography';


import { useState,useEffect } from 'react';
import {useNavigate} from 'react-router-dom';
import { getCsrfToken } from './CSRFToken';
import SchoolYearSelect from './SelectSchoolYear';
import { getCookie } from './CSRFToken';


function CreateBookPage(props){

    const[isbn,setIsbn]=useState(0);
    // const[info,setInfo]=useState("");
    const[title,setTitle]=useState("");
    const[author,setAuthor]=useState("");
    const[cover,setCover]=useState("");
    const[douban_url,setDouban]=useState("");
    const[publisher,setPublisher]=useState("");
    const[pubdate,setPubdate]=useState("");
    const[relationError,setRelationError]=useState("");
    const[teacher,setTeacher]=useState("");
    const[course,setCourse]=useState("");
    const[department,setDepartment]=useState("");
    const[school_year,setSchoolyear]=useState("");
    const[semester,setSemester]=useState("");
    const[isbnError,setIsbnError]=useState("");

    // const[doubanError,setDoubanError]=useState("");
    
    const navigate=useNavigate();

    const csrftoken = getCsrfToken();

    // useEffect(()=>{
    //     console.log(school_year)
    // },[school_year]);
    

    async function handleSearchButton(){
        console.log("username: "+getCookie('username'))
        if(getCookie('username')==null){
            navigate('/login',{state:{from:'createBook'}})
            return;
        }
        if(isbn.length !=13){
            setIsbnError("ISBN");
            return;
        }
        let response= await fetch("/api/get-douban-book"+"?isbn="+isbn)
        if(!response.ok){
            setIsbnError(response.msg);
            return;
        }
        setIsbnError("");
        await response.json()
        .then((data)=>{setTitle(data.title);
            setAuthor(data.author);
            setCover(data.images.small);
            setDouban(data.alt);
            setPublisher(data.publisher);
            setPubdate(data.pubdate);

            })
    }
    async function handleSubmit(){
        console.log("handling submit")
        if(isbn.length !=13){
            setIsbnError("ISBN");
            return;
        }
        if(!isbn||!title||!author||!pubdate||!cover||!douban_url||!teacher||!course||!department||!school_year||!semester){
            setRelationError("");
            return;
        }
        const requestOption={
            method:"POST",
            headers:{
                "Content-Type": "application/json",
                "X-CSRFToken": csrftoken
            },
            body:JSON.stringify({
                book:{isbn:isbn,
                    title:title,
                    author:author,
                    publisher:publisher,
                    pubdate:pubdate,
                    cover:cover,
                    douban_url:douban_url},
                teacher:{
                    teacher_name:teacher
                },
                course:{
                    course_name:course,
                    department:department
                },
                school_year:school_year,
                semester:semester
            }),
        };
        let response=await fetch("/api/create-book",requestOption)
        let data=await response.json();
        if(!response.ok){
            setRelationError(data.msg);
            return;
        }
        setRelationError("");
        navigate('/book/'+isbn);

    };

    return(
        <div className='createBookPage'>
            <Box  alignContent="center" sx={{ display:'flex',flexDirection: 'row'}}>
                <Box sx={{width:600,p: 2, border: '1px dashed grey'}}>{/*minWidth:400,maxWidth:400*/}
                <Grid container spacing={1} item xs={6} >
                    <Grid item xs={12} align="center">
                        <h1>This is create Use</h1>
                    </Grid>
                    <Grid item xs={12} align="center">
                                    <TextField label={'ISBN,'} onChange={(e)=>{setIsbn(e.target.value)}}></TextField>
                                    <Button variant="contained" onClick={handleSearchButton}></Button>
                                </Grid>
                    <Grid item xs={12} align="center">
                    {/* <TextField value={teacher} label={""} onChange={(e)=>{setTeacher(e.target.value)}}/> */}
                        <FormControl  variant="outlined">
                            <InputLabel htmlFor="teacher-input"></InputLabel>
                            <OutlinedInput
                                id="teacher-input"
                                value={teacher}
                                onChange={(e) => { setTeacher(e.target.value) }}
                                label=""
                            />
                        </FormControl>
                    </Grid>
                    
                    <Grid item xs={12} align="center">
                        <FormControl  variant="outlined">
                            <InputLabel htmlFor="course-input"></InputLabel>
                            <OutlinedInput
                                id="course-input"
                                value={course}
                                onChange={(e) => { setCourse(e.target.value) }}
                                label=""
                            />
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} align="center">
                        <FormControl sx={{ m: 1, minWidth: 150 }}>
                        <InputLabel id="demo-simple-select-helper-label"></InputLabel>
                            <Select
                            labelId="demo-simple-select-helper-label"
                            id="demo-simple-select-helper"
                            value={department}
                            label=""
                            onChange={(e)=>{setDepartment(e.target.value)}}
                            >
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>

                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} align="center">
                        <FormControl variant="outlined" sx={{ m: 1, minWidth: 120 }}>
                            <SchoolYearSelect onChange={(e)=>{setSchoolyear(e.target.value)}} value={school_year}/>
                        </FormControl>
                        <FormControl sx={{ m: 1, minWidth: 140 }}>
                            <InputLabel id="demo-simple-select-helper-label"></InputLabel>
                            <Select
                            labelId="demo-simple-select-helper-label"
                            id="demo-simple-select-helper"
                            value={semester}
                            label=""
                            onChange={(e)=>{setSemester(e.target.value)}}
                            >
                            <MenuItem value={1}>1-</MenuItem>
                            <MenuItem value={2}>2-</MenuItem>
                            </Select>
                        </FormControl>
                        <Button variant="contained" onClick={handleSubmit}></Button>
                        {relationError && <Alert severity="error" style={{ marginTop: 20 }}>{relationError}</Alert>}

                    </Grid>
                    </Grid>
                </Box>
                <Card >
                    <Box sx={{ display:'flex',flexDirection: 'row',minWidth:300}}>
                        <CardContent>
                            <Grid container spacing={1} item xs={12} >
                                <Grid item xs={12} >{/*align="center"*/}
                                    <h1>This is create book</h1>
                                </Grid>
                                <Grid item xs={12}>
                                    {/* <TextField value={title} label={""} onChange={(e)=>{setTitle(e.target.value)}}/> */}
                                    {/* <FormControl  variant="outlined">
                                        <InputLabel htmlFor="title-input"></InputLabel>
                                        <OutlinedInput
                                            id="title-input"
                                            value={title}
                                            onChange={(e) => { setTitle(e.target.value) }}
                                            label=""
                                        />
                                    </FormControl> */}
                                    <Typography variant='h3'>{title}</Typography>

                                </Grid>
                                <Grid item xs={12}>
                                    {/* <TextField value={author} label={""} onChange={(e)=>{setAuthor(e.target.value)}}/> */}
                                    {/* <FormControl  variant="outlined">
                                        <InputLabel htmlFor="author-input"></InputLabel>
                                        <OutlinedInput
                                            id="author-input"
                                            value={author}
                                            onChange={(e) => { setAuthor(e.target.value) }}
                                            label=""
                                        />
                                    </FormControl> */}
                                    <Typography variant='h6'>{author}</Typography>
                                </Grid>
                                <Grid item xs={12}>
                                {/* <TextField value={publisher} label={""} onChange={(e)=>{setPublisher(e.target.value)}}/> */}
                                    {/* <FormControl  variant="outlined">
                                        <InputLabel htmlFor="publisher-input"></InputLabel>
                                        <OutlinedInput
                                            id="publisher-input"
                                            value={publisher}
                                            onChange={(e) => { setPublisher(e.target.value) }}
                                            label=""
                                        />
                                    </FormControl> */}
                                    <Typography variant='h6'>{publisher}</Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    {/* <FormControl  variant="outlined"> */}
                                        {/* <InputLabel htmlFor="publisher-input">{}</InputLabel>
                                        <OutlinedInput
                                            id="publisher-input"
                                            value={pubdate}
                                            onChange={(e) => { setPubdate(e.target.value) }}
                                            label=""
                                        />
                                    </FormControl> */}
                                    <Typography variant='h6'>{pubdate}</Typography>
                                </Grid>
                            </Grid>
                            {isbnError && <Alert severity="error" style={{ marginTop: 20 }}>{isbnError}</Alert>}
                        </CardContent>
                        <CardMedia
                        component="img"
                        image={cover}
                        title='cover'
                        />
                    </Box>
                </Card>
            </Box>
        </div>
    );
}

export default CreateBookPage;